import firebase from "../services/firebase";

class PostsController {
  constructor() {}

  async getPosts() {
    const posts = await firebase.database().ref('posts');
    
    return new Promise((resolve) => {
      posts.on("value", function(snapshot) {
        const array = new Array();

        snapshot.forEach(function(childSnapshot) {
          const childKey = childSnapshot.key;
          const childData = childSnapshot.val();

          array.push({
            id: childKey,
            alt: childData.alt,
            src: childData.src,
            title: childData.title,
            categories: childData.categories
          });
        });
        
        resolve(array);
      });
    });
  }

  async getPostByid(id) {
    const posts = await firebase.database().ref('posts/' + id);
    
    return new Promise((resolve) => {
      posts.on("value", function(snapshot) {
        const post = snapshot.val();
        resolve({
          alt: post.alt,
          src: post.src,
          categories: post.categories,
          content: post.content,
          title: post.title,
          technical: post.technical,
          localisation: post.localisation,
          date: post.date
        }); 
      });
    });
  }

  async insertPosts(post, callback) {
    try {
      await firebase.database().ref('posts').push(post);
      callback();
    } catch (error) {
      console.log(error);
      return error;
    }
  }
}
  
export default PostsController;