<template>
  <div id="post" class="paddsection">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-8 col-md-offset-2">
          <div class="row">
            <div class="container-main single-main">
              <div class="col-md-12">
                <div class="block-main mb-30">
                  <img :src="post.src" class="img-responsive" :alt="post.alt">
                  <div class="content-main single-post padDiv">
                    <div class="journal-txt">
                      <h4>{{ post.title }}</h4>
                    </div>
                    <div class="post-meta">
                      <ul class="list-unstyled mb-0">
                        <li class="author">{{ post.categories }}</li>
                        <li class="author">{{ post.localisation }}</li>
                        <li class="author">{{ post.technical }}</li>
                        <li class="date">{{ post.date }}</li>
                      </ul>
                    </div>
                    <p class="mb-30">{{ post.content }}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import PostController from "../controllers/posts.controller"

  export default {
    name: 'post',
    data: function () {
      return {
        post: {}
      }
    },
    methods: {
      async getPostbyId() {
        const postController = new PostController();
        this.post = await postController.getPostByid(this.$route.params.id);
      }
    },
    mounted() {
      this.getPostbyId();
    }
  }
</script>