<template>
<div id="portfolio" class="text-center">

<div class="container">
  <div class="section-title text-center" data-aos="fade-zoom-in">
    <h2>Portfolio</h2>
  </div>
</div>

<div class="container">
  <div class="row">
    <div class="col-md-12">

      <div class="portfolio-list" >
        <ul class="nav list-unstyled" id="portfolio-filters">
          <li v-for="(val, key) in option.getFilterData" :key="key" v-on:click="filter(key)" :class="[key===filterOption? 'filter filter-active' : 'filter']">{{ key }}</li>
        </ul>
      </div>

      <div class="portfolio-container">
        <isotope ref="cpt" :options='option' :list="artworks" @filter="filterOption=arguments[0]">
          <div v-for="(element, index) in artworks" :key="index" class="col-lg-4 col-md-6 col-xs-12 portfolio-thumbnail">
            <router-link :to="{ name: 'Post', params: { id: element.id }}" class="popup-img">
              <img :src="element.src" :alt="element.alt">
              <h4>{{ element.title }}</h4>
            </router-link>
          </div> 
        </isotope>
      </div>
    </div>
  </div>
</div>

</div>
</template>

<script>
import isotope from 'vueisotope';
import PostController from "../controllers/posts.controller"

export default {
  data: function () {
    return {
      artworks: [],
      filterOption: "show all",
      option: {
        itemSelector: ".portfolio-thumbnail",
        getFilterData: {
          "All": function() {
            return true;
          },
          "Artistes": function(el) {
            return el.categories === "Artistes";
          },
          "Voyages": function(el) {
            return el.categories === "Voyages";
          },
          "Portraits": function(el) {
            return el.categories === "Portraits";
          }
        }
      }
    }
  },
  components: {
    isotope
  },
  methods: {
    filter(key) {
      this.$refs.cpt.filter(key);
    },
    async getPosts() {
      const postController = new PostController();
      this.artworks = await postController.getPosts();
    }
  },
  mounted() {
    this.getPosts();
  }
}
</script>