<template>
  <div id="app">
    <Navbar />
    <router-view></router-view>
    <Footer />
    <Loader v-bind:loader="loader" />
  </div>
</template>

<script>
import Navbar from './components/navbar.vue';
import Loader from './components/loader.vue';
import Footer from './components/footer.vue';

export default {
  name: 'App',
  components: {
    Loader,
    Footer,
    Navbar
  },
  data: function () {
    return {
      loader: true 
    }
  },
  methods:{
    init() {
      setTimeout(() => { 
        this.loader = false
      }, 3000);
    }
  },
  mounted(){
    this.init()
  }
}
</script>
