<template>
  <div id="admin" class="text-center">
    <div class="section-title">
      <h2>Administration</h2>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-lg-6">
          <div class="form-group contact-block1">
            <input type="text" name="title" class="form-control" id="name" placeholder="Titre" v-model="title" />
            <div class="validation"></div>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="form-group">
            <input type="text" class="form-control" name="technical" id="technical" placeholder="Focale – ****" v-model="technical" />
            <div class="validation"></div>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="form-group">
            <select class="form-control" v-model="categories">
              <option :value="{ category: 'Voyages'}">Voyages</option>
              <option :value="{ category: 'Artistes'}">Artistes</option>
              <option :value="{ category: 'Portraits'}">Portraits</option>
            </select>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="form-group">
            <input type="date" class="form-control" v-model="date" />
          </div>
        </div>
        <div class="col-lg-6">
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Lieu" v-model="localisation" />
            <div class="validation"></div>
          </div>
        </div>
        <div class="col-lg-6">
          <div class="form-group">
            <input type="text" class="form-control" placeholder="Url google drive" v-model="img" />
            <div class="validation"></div>
          </div>
        </div>
        <div class="col-lg-12">
          <div class="form-group">
            <textarea class="form-control" name="message" rows="12" v-model="content" placeholder="Message"></textarea>
            <div class="validation"></div>
          </div>
        </div>
        <div class="col-lg-12">
          <input type="submit" class="btn btn-defeault btn-send" @click="addPost" value="Ajouter un article">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import PostController from "../controllers/posts.controller"

export default {
  name: 'Admin',
  data: function () {
    return {
      title: '',
      categories: '',
      content: '',
      technical: '',
      localisation: '',
      date: '',
      img: ''
    }
  },
  methods: {
    getUrlImg(url) {
      return url.replace('open', 'uc')
    },
    addPost() {
      const postController = new PostController();
      const img = this.getUrlImg(this.img);
      postController.insertPosts({
        alt: "Lucas Wils - Photographe",
        src: img,
        categories: this.categories.category,
        content: this.content,
        title: this.title,
        technical: this.technical,
        localisation: this.localisation,
        date: new Date(this.date)
      }, () => {
        alert('Article: ' + this.title + ' créé');
        this.title = '';
        this.content = '';
        this.category = '';
        this.technical = '';
        this.date =  '';
        this.img = '';
        this.localisation = '';
      });
    }
  }
}
</script>